import { BridgeComponent } from "@hotwired/hotwire-native-bridge";
class BarcodeComponent extends BridgeComponent {
    static component = "barcode";

    connect() {
        super.connect();
    }

    scanAndSearch(data = "") {
        if (data) {
            this.performSearch(data);
        }
    }

    performSearch(code) {
        const event = new CustomEvent("turbo:next-search", { detail: { code } });
        window.dispatchEvent(event);
    }
}
export default BarcodeComponent;
