import { BridgeComponent } from "@hotwired/hotwire-native-bridge";

class ToastComponent extends BridgeComponent {
    static component = "toast";

     connect() {
        super.connect();

        // Trigger Component Ready Event
        window.dispatchEvent(new CustomEvent('bagisto-native:toast-ready'));
    }

    show(event) {
        event.stopImmediatePropagation();
        event.preventDefault();
        this.#showToast();
    }

    #showToast() {
        const message = this.bridgeElement.bridgeAttribute("message");
        this.send("show", { message }, () => {});
    }
}

export default ToastComponent;
