import { BridgeComponent } from "@hotwired/hotwire-native-bridge";
import BarcodeComponent from "./barcode_component_controller.js";

class DynamicButtonComponent extends BridgeComponent {
    static component = "dynamicbutton";

    connect() {
        super.connect();

        // Trigger Component Ready Event
        window.dispatchEvent(new CustomEvent('bagisto-native:dynamic-button-ready'));

        const pageType = this.element.dataset.pageType;
        if (pageType === "home") {
            this.send("home", {}, (response) => {
                    const data = response?.data || {};
                    const type = data?.type; // e.g., "cart", "scan", etc.
                    this.handleAction(type, data);
                },
            );
        }
        
        // Single Product Page Share and Cart.
        if (pageType === "product") {
            this.send("product", {}, (response) => {
                const data = response?.data || {};
                const type = data?.type; // e.g., "cart", "scan", etc.
                this.handleAction(type, data);
            });
        }

        // Empty the Dynamic Button Icons
        if (pageType === "empty") {
            this.send("empty", {}, (response) => {});
        }
    }

    handleAction(type, data = {}) {
        const code = data?.code;
        switch (type) {
            case "cart":
                const event = new CustomEvent("turbo:next-cart-modal");
                window.dispatchEvent(event);
                break;

            case "scan":
                let barcode = new BarcodeComponent();
                barcode.scanAndSearch(code);
                break;
        }
    }

    sendCartCount() {
        const count = this.bridgeElement.bridgeAttribute("cartcount");
        this.send("cartcount", { count:String(count ?? 0) }, () => {});
    }

    // send modal open event
    sendModalOpenEvent() {
        this.send("modalopen", {}, (response) => {
            const data = response?.data || {};
            const type = data?.type;
            if(type === "modal_dismiss") {
                window.dispatchEvent(new CustomEvent("bagisto-native:modal-dismiss"));
            }
        });
    }

    // send modal close event
    sendModalDismissEvent() {
        this.send("modaldismiss", {}, () => {});
    }
}
export default DynamicButtonComponent;

