import { BridgeComponent } from "@hotwired/hotwire-native-bridge";

class ShareLocationComponent extends BridgeComponent {
    static component = "location";

    connect() {

        // Trigger Component Ready Event
        window.dispatchEvent(new CustomEvent('bagisto-native:location-component-ready'));

        try {
            super.connect();

            if (!this.boundClickHandler) {
                this.boundClickHandler = this.shareLocation.bind(this);
                this.element.addEventListener("click", this.boundClickHandler);
            }
            // Native sends back coordinates directly here
            this.send("addLocationButton", {}, (response) => {

                this.handleLocationResponse(response);
            });
        } catch (err) {
            console.error("BridgeComponent connect error:", err);
        }
    }

    disconnect() {
        if (this.boundClickHandler) {
            this.element.removeEventListener("click", this.boundClickHandler);
            this.boundClickHandler = null;
        }
    }

    shareLocation() {
        this.sendToast('Native location already fetched on connect. Click action does nothing now.')
    }

    handleLocationResponse(response) {
        const isTurboNativeAndroid = navigator.userAgent.includes('Turbo Native Android');
        const coords = isTurboNativeAndroid
            ? response.data
            : response.data?.coordinates;
        if (!coords || coords.latitude == null || coords.longitude == null) {
            this.sendToast("Could not fetch location.");
            return;
        }

        const { latitude, longitude } = coords;
        const url = `https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${latitude}&lon=${longitude}`;

        fetch(url)
            .then((res) => res.json())
            .then((data) => {
                const event = new CustomEvent("turbo:hotwire-app-fill-addresses", { detail: { data } });
                window.dispatchEvent(event);

                this.sendToast(
                    "Address autofilled."
                );
            })
            .catch((err) => {
                this.sendToast(
                    "Could not reverse geocode the location."
                );
            });
    }

    sendToast(message) {
       if (typeof document === "undefined") return;
        let nativeToast = document.querySelector(
				"[data-controller='bridge--toast']"
			); 
        if(nativeToast) {
            nativeToast.setAttribute('data-bridge-message', message);
            nativeToast.click();
        }

    }
}
export default ShareLocationComponent;
