import { BridgeComponent } from "@hotwired/hotwire-native-bridge";

class ThemeModeComponentController extends BridgeComponent {
    static component = "thememode";

    connect() {
        super.connect();

        // Trigger Component Ready Event
        window.dispatchEvent(new CustomEvent('bagisto-native:theme-mode-ready'));
    }

    handleClick(event) {
        event.preventDefault();
        event.stopImmediatePropagation();
        this.#handleAction();
    }

    #handleAction() {
        const mode =
            this.bridgeElement.bridgeAttribute("mode") === "dark"
                ? "dark"
                : "light";
        this.send("mode", { mode });
    }
}

export default ThemeModeComponentController;


