import { BridgeComponent } from "@hotwired/hotwire-native-bridge";
class Search extends BridgeComponent {
    static component = "search";

    connect() {
        super.connect();

        // Trigger Component Ready Event
        window.dispatchEvent(new CustomEvent('bagisto-native:search-component-ready'));

        this.lastQuery = "";
        this.debounceTimer = null;

        this.send("connect", {}, (message) => {
            const query = message.data?.query?.trim();
            if (!query || query.length < 2) return;

            clearTimeout(this.debounceTimer);

            this.debounceTimer = setTimeout(() => {
                this.performRedirect(query);
            }, 600);
        });
    }

    performRedirect(query) {
        const event = new CustomEvent("turbo:next-search", { detail: { query } });
        window.dispatchEvent(event);
    }
}
export default Search;
