import "@hotwired/turbo";
import { Application } from "@hotwired/stimulus";
import Search from "./controllers/search_controller.js";
import BarcodeComponent from "./controllers/barcode_component_controller.js";
import ShareLocationComponent from "./controllers/share_location_component_controller.js";
import DynamicButtonComponent from "./controllers/dynamic_button_component_controller.js";
import ToastComponent from "./controllers/toast_component_controller.js";
import HistorySyncComponentController from "./controllers/history_sync_component_controller.js";
import ThemeModeComponentController from "./controllers/theme_mode_component_controller.js";

window.Stimulus = Application.start();

Turbo.session.drive = true;

Stimulus.register("bridge--search", Search); // search functionaltiy
Stimulus.register("bridge--barcode", BarcodeComponent); // Used with dynamic button controller and having feature image-search and qr code search
Stimulus.register("bridge--location", ShareLocationComponent); // Location feature
Stimulus.register("bridge--dynamicbutton", DynamicButtonComponent); // cart, scan, image-serach, share and other functionality
Stimulus.register("bridge--toast", ToastComponent); // Toast message feature
Stimulus.register("bridge--historysync", HistorySyncComponentController ); // send the current uri
Stimulus.register("bridge--thememode", ThemeModeComponentController); // Theme mode component controller
